.section{
  padding: 50px 0;
  overflow: hidden;
  @include media('<lg'){
    padding: 30px 0;
  }
  @include media('<md'){
    padding: 20px 0;
  }
  &.intro{
    padding-top: 0;
    @include media('>lg'){
      padding-right: 2rem;
    }
    h1{
      overflow: hidden;
    }
  }
  &__title{
    overflow: hidden;
  }
  p{
    overflow: hidden;
  }
}